  border: 1px solid #ddd;
  box-shadow: none; }

.primary-navigation .sub-menu {
  box-shadow: 0 23px 38px rgba(0, 0, 0, 0.3), 0 19px 12px rgba(0, 0, 0, 0.22); }

.smm-mega-menu {
  padding: 20px; }
  .smm-mega-menu .smm-span-2,
  .smm-mega-menu .smm-post-10 {
    width: 100%;
    margin: 0 auto; }

.mm-overlay {
  display: none; }
  .mm-overlay.mm-hidden {
    z-index: -1; }

@media (min-width: 1024px) {
  .mm-overlay {
    display: block;
    content: "";
    background-color: rgba(0, 0, 0, 0.65);
    opacity: 0;
    transition: 300ms ease-in-out;
    width: 100%;
    height: 100%;
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0; }
    .mm-overlay.mm-active {
      opacity: 1;
      transition: 300ms ease-in-out;
      z-index: 100; } }

.home .site-content .entry-header {
  display: none; }

#ilsv-homepage-hero-image {
  background-position: 50%;
  background-repeat: no-repeat;
  background-size: 250%;
  margin-top: 0px;
  max-height: 475px; }
  #ilsv-homepage-hero-image p {
    color: #222; }
  @media (min-width: 375px) {
    #ilsv-homepage-hero-image {
      background-size: cover; } }
  @media (min-width: 768px) {
    #ilsv-homepage-hero-image {
      background-position: center center;
      max-height: none;
      min-height: 60vh; }
      #ilsv-homepage-hero-image p {
        color: inherit; }
      #ilsv-homepage-hero-image .elementor-background-overlay {
        display: none; } }

.ilsv-intro-icons .elementor-row {
  align-items: center;
  justify-content: space-between; }

.ilsv-intro-icons .elementor-column {
  width: 45%; }
  @media (min-width: 768px) {
    .ilsv-intro-icons .elementor-column {
      width: inherit; } }

.ilsv-home-image-block {
  min-height: 500px; }
  @media (min-width: 768px) {
    .ilsv-home-image-block {
      min-height: inherit; } }
  .ilsv-home-image-block p {
    line-height: 1.25; }
    @media (min-width: 1024px) {
      .ilsv-home-image-block p {
        line-height: inherit; } }

.cart {
  display: none; }

.archive .site-main {
  max-width: 1600px;
  padding: 10px;
  margin: 0 auto; }

.single-product div.product {
  max-width: 1600px;
  padding: 10px;
  margin: 0 auto; }
